<template>
    <Navbar/>
    <div class="header">
      <div class="card">
        <div class="card-top"></div>
        <div class="avatar-holder">
          <img :src="user.profile?.profilePicture" alt="pfp">
        </div>
        <div class="name">
          <h3>{{ user.profile?.username }}</h3>
          <p>{{ user.profile?.biography }}</p>
        </div>
        <div class="card-info">
          <div class="followers">
            <h6>Követők <i class="fa fa-users" aria-hidden="true"></i></h6>
            <p>0</p>
          </div>
          <div class="following">
            <h6>Követés <i class="fa fa-user-plus" aria-hidden="true"></i></h6>
            <p>0</p>
          </div>
          <div class="posts">
            <h6>Bejegyzések <i class="fas fa-edit"></i></h6>
            <p>0</p>
          </div>
        </div>
        <div class="card-footer">
          <h4 v-if="user.privacy?.showName">Teljes név</h4>
          <h5 v-if="user.privacy?.showName">{{ user.profile?.name }}</h5>
          <h4 v-if="user.privacy?.showEmail">Email cím</h4>
          <h5 v-if="user.privacy?.showEmail">{{ user.profile?.email }}</h5>
          <h4 v-if="!user.privacy?.showName && !user.privacy?.showEmail">A felhasználó adatai privátak</h4>
        </div>
      </div>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { userRoles } from '@/assets/js/functions.js';
import Navbar from '@/components/Navbar';
import '@/assets/scss/profile.scss';

export default {
    name: 'Profile',
    components: {
        Navbar
    },

    data() {
        return {
        }
    },

    computed: {
        ...mapGetters(['user', 'userError']),
    },

    methods: {
        ...mapActions(['getUser', 'addNotification']),

        checkUserRoles() {
        if(this.user.profile?.roles == undefined) {
            this.$router.push('/notfound');
            const notification = {
            message: this.userError,
            type: 'error'
            }

            this.addNotification(notification);
            return;
        }
        userRoles(this.user.profile.roles);
        },
    },

    async created() {
        await this.getUser(this.$route.params.username);
        this.checkUserRoles();
    }
}
</script>
  