<template>
    <div class="header">
        <div class="header-main">
            <div class="logo">
                <router-link to="/">Wolimby</router-link>
            </div>
            <div class="open-nav-menu" @click="toggleNav">
                <span></span>
            </div>
            <div class="menu-overlay" @click="toggleNav"></div>
            <nav class="nav-menu" @click="navMenuOpen">
                <div class="close-nav-menu" @click="toggleNav">
                    <i class="fa-solid fa-circle-xmark"></i>
                </div>
                <ul class="menu">
                    <!-- <li class="menu-item">
                        <router-link to="">Rólunk</router-link>
                    </li> -->
                    <li class="menu-item menu-item-has-children" v-if="isLoggedIn">
                        <a href="#" data-toggle="sub-menu"><img :src="currentUser?.profile?.profilePicture" class="pfp" alt="pfp"></a>
                        <ul class="sub-menu">
                            <li class="menu-item"><router-link :to="'/user/' + currentUser?.profile?.username">Profilom</router-link></li>
                            <li class="menu-item"><router-link to="/settings">Beállítások</router-link></li>
                            <li class="menu-item"><a @click="logoutUser">Kijelentkezés</a></li>
                        </ul>
                    </li>
                </ul>
            </nav>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import '@/assets/scss/navbar.scss';
export default {
  name: 'Navbar',
  data() {
    return {
    };
  },

  computed: {
    ...mapGetters(['isLoggedIn', 'currentUser', 'userError']),
  },

  methods: {
    ...mapActions(['getCurrentUser', 'logout', 'addNotification']),

    toggleNav() {
        document.querySelector('.nav-menu').classList.toggle('open');
        document.querySelector('.menu-overlay').classList.toggle('active');
        document.body.classList.toggle('hidden-scrolling');
    },

    navMenuOpen(event) {
        if(event.target.hasAttribute('data-toggle') && window.innerWidth <= 991) {
            event.preventDefault();
            const menuItemHasChildren = event.target.parentElement;
            if(menuItemHasChildren.classList.contains('active')) {
                this.collapseSubMenu();
            }
            else {
                if(document.querySelector('.nav-menu').querySelector('.menu-item-has-children.active')) {
                    this.collapseSubMenu();
                }

                menuItemHasChildren.classList.add('active');
                const subMenu = menuItemHasChildren.querySelector('.sub-menu');
                subMenu.style.maxHeight = subMenu.scrollHeight + 'px';
            }
        } 
    },

    collapseSubMenu() {
        document.querySelector('.nav-menu').querySelector('.menu-item-has-children.active .sub-menu').removeAttribute('style');
        document.querySelector('.nav-menu').querySelector('.menu-item-has-children.active').classList.remove('active');
    },

    resizeFix() {
        if(navMenu.classList.contains('open')) {
  	 	    this.toggleNav();
        }

        if(navMenu.querySelector('.menu-item-has-children.active')) {
            this.collapseSubMenu();
        }
    },

    whenResize() {
        if(this.innerWidth > 991) {
            this.resizeFix();
        }
    },

    siteSettings() {
        if(this.currentUser.appearance.backgroundImage != 'none') {
            document.body.style.backgroundImage = 'url(' + this.currentUser.appearance.backgroundImage + ')';
            document.body.style.backgroundRepeat = 'no-repeat';
            document.body.style.backgroundSize = 'auto';
            document.body.style.animation = 'none';
        }
    },

    logoutUser() {
      this.logout();
      this.$router.push('/');

      this.addNotification({
        message: 'Sikeres kijelentkezés.',
        type: 'success'
      });
    },
  },

  async mounted() {
    this.isLoggedIn ? await this.getCurrentUser() : void(0);
    this.siteSettings();
    window.addEventListener('resize', this.whenResize());
  },
}
</script>